import { Bar } from "react-chartjs-2";
import { chart as chartJS } from "chart.js/auto"

const miDataBar = {
    labels: ["Lunes", "Martes", "Miercoles", "Jueves", "Viernes", "Sabado", "Domingo"],
    datasets: [{
        label: 'My First Dataset',
        data: [65, 59, 80, 81, 56, 55, 40],
        backgroundColor: [
            'rgba(255, 99, 132, 0.2)',
            'rgba(255, 159, 64, 0.2)',
            'rgba(255, 205, 86, 0.2)',
            'rgba(75, 192, 192, 0.2)',
            'rgba(54, 162, 235, 0.2)',
            'rgba(153, 102, 255, 0.2)',
            'rgba(201, 203, 207, 0.2)'
        ],
        borderColor: [
            'rgb(255, 99, 132)',
            'rgb(255, 159, 64)',
            'rgb(255, 205, 86)',
            'rgb(75, 192, 192)',
            'rgb(54, 162, 235)',
            'rgb(153, 102, 255)',
            'rgb(201, 203, 207)'
        ],
        borderWidth: 1
    }]
};
const miOption = {
    scales: {
        x: {
            grid: {
                color: function (context) {
                    return context.index === 0 ? ("rgba(255, 255, 255)") : ("rgba(0, 0, 0, 0)")
                }
            }
        },
        y: {
            grid: {
                color: function (context) {
                    return context.index === 0 ? ("rgba(255, 255, 255)") : ("rgba(0, 0, 0, 0)")
                }
            }
        }
    },
    plugins: {
        legend: {
            display: false
        },
    },
}

function UserGraphBar() {
    return <Bar data={miDataBar} options={miOption} />
}

export default UserGraphBar